footer {
  text-align: center;
  position: relative;
  bottom: 0;
  width: 100%;
  padding: 0 15% 0 11%;
}

footer p {
  font-size: 15px;
}

.social-icons-footer li {
  display: inline;
  margin: 0% 1%;
  opacity: 50%;
}

.social-icons-footer li i,
.social-icons-footer a {
  font-size: 24px;
}